$('.usertype-selector select').change(function(){
	var _that = $(this),
				distributor = $('.distributor-fields'),
				online = $('.online-fields');
				passwordFields = $('.password-fields')

	if(_that.val() == 'B2B'){
		distributor.slideDown();
		online.slideUp();
		passwordFields.slideDown();
	} else if(_that.val() == 'B2E'){
		distributor.slideUp();
		online.slideDown();
		passwordFields.slideDown();
	} else {
		distributor.slideUp();
		online.slideUp();
		passwordFields.slideDown();
	}
});


