var howl = $("#owl-home");
var towl = $("#testimonial-carousel");
new Imager('.js-responsive-image');
if (howl.children().length > 1) {
	howl.owlCarousel({
	    loop:true,
	   	items:1
	});
} else {
	howl.show();
}
if (towl.children().length > 1) {
	towl.owlCarousel({
	    loop:true,
	   	items:1
	});
} else {
	towl.show();
}




