$('.navbar-toggle').click(function(event) {
            event.preventDefault()
            if ($('.pri-nav').hasClass('in')) {
                $(this).removeClass('active').trigger('blur');
                $('.pri-nav').removeClass('in');
            } else {
                $(this).addClass('active');
                $('.pri-nav').addClass('in');
            }
        });


