var owl = $('.orange-carousel');
var powl = $('.pdp-carousel');
if (owl.children().length > 1) {
owl.owlCarousel({
      responsive:{
      0:{
            items:1
        },
        764:{
            items:3
        },
        992:{
            items:4
        }
    }
  });
}
if (powl.children().length > 1) {
	powl.owlCarousel({
	    loop:true,
	   	responsive:{
	   		0:{
	            items:4
	        },
	        992:{
	            items:5
	        }
	   	},
	   	margin:10,
	   	nav:true,
	   	navText:['',''],
	   	dots:false,
	});
}


