var options = {
  valueNames: [ 'name', 'list-code' ],
  page :10
};

var userList = new List('switch-user', options);

$('.account-switch').on('click','li',function(){
	var _that = $(this),code;
	if(_that.find('.fa').length == 0){
		$('.account-switch li .fa').remove();
		_that.addClass('active');
		code = _that.find('.list-code label').text();
		$('#unit').val(code);
		_that.append('<i class="fa fa-check"></i>');
		_that.parents('form').submit();
	}
});


